.screen2 {
	padding: 40px 0 220px 0;
	overflow: hidden;
	@media (max-width: 1920px){
		padding: 2.08vw 0 11.46vw 0;
		
	}
	@media (max-width: 1024px){
		background-color: #26292D;
		padding: 9.11vw 0 14.32vw;
	}
	&__title {
		max-width: 640px;
		@media (max-width: 1920px){
			max-width: 33.33vw;
			
		}
		@media (max-width: 1024px){
			max-width: 60%;
		}
	}
	&__text {
		max-width: 430px;
		margin-top: 50px;
		padding-left: 45px;
		@media (max-width: 1920px){
			max-width: 22.40vw;
			margin-top: 2.60vw;
			padding-left: 2.34vw;
			
		}
		@media (max-width: 1024px){
			max-width: 48.18vw;
			padding: 0;
			margin-top: 3.91vw;
		}
		@media (max-width: 767px){
			max-width: 63.68vw;
		}
	}
	&__items {
		margin-top: 30px;
		display: flex;
		justify-content: center;
		position: relative;
		@media (max-width: 1920px){
			margin-top: 1.56vw;
			
		}
		@media (max-width: 1024px){
			margin-top: 6.25vw;
		}
	}
	&__row {
		flex: 0 1 980px;
		display: flex;
		justify-content: space-between;
		z-index: 3;
		@media (max-width: 1920px){
			flex: 0 1 51.04vw;
			
		}
		@media (max-width: 1024px){
			flex: 1 1 auto;
			flex-wrap: wrap;
		}
	}
	&__item {
		flex: 0 1 33.333%;
		@media (max-width: 1024px){
			flex: 1 1 100%;
		}
	}
}
.item-screen2 {
	display: flex;
	flex-direction: column;
	align-items: center;
	@media (max-width: 1024px){
		flex-direction: row;
	}
	@media (max-width: 767px){
		margin-top: 8.42vw;
		&:first-child{
			margin: 0;
		}
	}
	&__icon {

		position: relative;
		img {
			width: 207px;
			height: 209px;
			transition: all 0.1s ease 0s;
			@media (max-width: 1920px){
				width: 10.78vw;
				height: 10.89vw;
				
			}
			@media (max-width: 1024px){
				width: 25.04vw;
				height: 25.04vw;
			}
			@media (max-width: 767px){
				width: 36.32vw;
				height: 36.32vw;
			}


			transform: translate(0, 0);
			&.js-top1 {
				transform: translate(0, -20%);
				
			}
			&.js-bottom1 {
				transform: translate(0, -12%);
				
			}
			&.js-top2 {
				transform: translate(0, -18%);
				
			}
			&.js-bottom2 {
				transform: translate(0, -15%);

			}
		}
	}
	&__circle {
		position: absolute;
		z-index: -1;
		border-radius: 50%;
		transform: translate(-75%, 0);
		left: 50%;
		bottom: 0;
		width: 120px;
		height: 120px;
		background-color: #D61F24;
		transition: all 0.1s ease 0s;
		@media (max-width: 1920px){
			width: 6.25vw;
			height: 6.25vw;
			
		}
		@media (max-width: 1024px){
			width: 19.53vw;
			height: 19.53vw;
			transform: translate(-50%, 0);
		}
		@media (max-width: 767px){
			width: 23.68vw;
			height: 23.68vw;
		}


		transform: translate(-70%, 0) scale(1);
		&.js-top1 {
			transform: translate(-70%, 20%) scale(1.2);
			
		}
		&.js-bottom1 {
			transform: translate(-70%, 12%) scale(1.1);
			
		}
		&.js-top2 {
			transform: translate(-70%, 18%) scale(1.17);
			
		}
		&.js-bottom2 {
			transform: translate(-70%, 15%) scale(1.14);

		}
	}

	&__body {
		@media (max-width: 1024px){
			margin-left: 12.37vw;
		}
		@media (max-width: 767px){
			margin-left: 7.89vw;
		}
	}

	&__title {
		margin-top: 40px;
		color: var(--2, #FFF);
		text-align: center;
		font-family: Montserrat;
		font-size: 36px;
		font-style: normal;
		font-weight: 700;
		line-height: 100%; /* 36px */
		text-transform: uppercase;
		@media (max-width: 1920px){
			margin-top: 2.08vw;
			font-size: 1.88vw;

		}
		@media (max-width: 1024px){
			font-size: 4.56vw;
			margin-top: 0;
			text-align: left;
		}
		@media (max-width: 767px){
			font-size: 5.26vw;
		}
	}
	&__subtitle {
		margin-top: 20px;
		color: var(--5, #7F89A7);
		text-align: center;
		font-size: 20px;
		font-style: normal;
		font-weight: 500;
		line-height: 100%; /* 20px */
		text-transform: uppercase;
		@media (max-width: 1920px){
			margin-top: 1.04vw;
			font-size: 1.04vw;

		}
		@media (max-width: 1024px){
			font-size: 1.95vw;
			margin-top: 1.95vw;
			text-align: left;
		}
		@media (max-width: 767px){
			font-size: 2.63vw;
		}
	}


	&:hover{
		.item-screen2__circle {
			/* animation: screen2-anim-circle 1s linear 0s alternate forwards; */
		}
	}
}


