.screen4 {
	padding: 40px 0 220px 0;
	overflow: hidden;
	@media (max-width: 1920px){
		padding: 2.08vw 0 11.46vw 0;
	}
	@media (max-width: 1024px){
		background-color: #26292D;
		padding: 9.11vw 0 0;
		overflow: hidden;
	}
	.swiper-slide {
		position: relative;
	}
	&__title {
	}
	&__row {
		margin-top: 75px;
		display: flex;
		justify-content: space-between;
		max-width: 100%;
		@media (max-width: 1920px){
			margin-top: 3.91vw;
		}
		@media (max-width: 1024px){
			flex-direction: column;
			margin-top: 6.51vw;
		}
	}
	&__left {
		max-width: 314px;
		@media (max-width: 1920px){
			max-width: 16.35vw;
		}
		@media (max-width: 1024px){
			max-width: 100%;
			order: -1;
		}
	}
	&__subtitle {
		color: var(--2, #FFF);
		font-size: 28px;
		font-style: normal;
		font-weight: 700;
		line-height: 100%; /* 28px */
		text-transform: uppercase;
		@media (max-width: 1920px){
			font-size: 1.46vw;
		}
		@media (max-width: 1024px){
			font-size: 1.95vw;
			display: flex;
			align-items: center;
		}
		@media (max-width: 767px){
			font-size: 2.63vw;
		}

		&--mobile {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
		}
	}
	&__center {
		position: relative;
		width: 550px;
		max-width: 100%;
		opacity: 1;
		transition: all 0.3s ease 0s;
		@media (max-width: 1920px){
			width: 28.65vw;
		}
		@media (max-width: 1024px){
			opacity: 1;
			width: 100%;
			min-height: 55.99vw;
			display: flex;
			justify-content: center;
			align-items: center;

		}
		@media (max-width: 767px){
			min-height: 70vw;
		}
		&.js-active {
			opacity: 1;
		}
		.screen4__center-inner {
			min-height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			opacity: 1;
			transition: all 0.3s ease 0s;
			@media (max-width: 1024px){
				opacity: 1;
				position: relative;
				width: 26.04vw;
			}
			@media (max-width: 767px){
				width: 32.89vw;
			}
			img {
				position: relative;
				z-index: 2;
				width: 331px;
				@media (max-width: 1920px){
					width: 17.24vw;
					
				}
				@media (max-width: 1024px){
					width: 100%;
				}
			}
			&.js-active {
				animation: image-center 0.3s ease 0s forwards;
				@keyframes image-center {
					0% {
						opacity: 0;
					}
					100% {
						opacity: 1;
					}
				}
			}
		}
		.swiper-slide {
			opacity: 0!important;
			&.js-active {
				opacity: 1!important;
			}
		}
	}
	&__right {
		max-width: 314px;
		@media (max-width: 1920px){
			max-width: 16.35vw;
			
		}
		@media (max-width: 1024px){
			order: -1;
			max-width: 100%;

		}
	}



	.swiper-arrow {
		cursor: pointer;
		margin-left: 40px;
		width: 22px;
		height: 12px;
		@media (max-width: 1920px){
			margin-left: 2.08vw;
			width: 1.15vw;
			height: 0.63vw;
		}
		@media (max-width: 1024px){
			width: 2.60vw;
			min-width: 2.60vw;
			height: 2.60vw;
			margin-left: 0;
		}
		svg {
			width: 100%;
			height: 100%;
		}
	}
	.swiper-arrow-prev {
		@media (max-width: 1024px){
			transform: rotate(-90deg);
		}
	}
	.swiper-arrow-next {
		@media (max-width: 1024px){
			transform: rotate(-90deg);
		}
	}
}

.left-screen4 {
	&__swiper-wrapper {
		margin-top: 40px;
		@media (max-width: 1920px){
			margin-top: 2.08vw;
		}
		@media (max-width: 1024px){
			display: flex;
			align-items: center;
			margin-top: 0;
		}
		@media (max-width: 767px){
			padding: 6.58vw 0;
		}
	}
	&__swiper {
		max-height: 510px;
		@media (max-width: 1920px){
			max-height: 26.56vw;
		}
	}
	&__slide {
		padding: 20px 0;
		@media (max-width: 1920px){
			padding: 1.04vw 0;
		}
		@media (max-width: 1024px){
			padding: 4.56vw 0;
		}
		@media (max-width: 767px){
			padding: 0;
		}
	}
}

.left-center-screen4 {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	min-height: 100%;
	display: flex;
	transition: all 0.3s ease 0s;
	&__circle {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 475px;
		height: 475px;
		transform: translate(-50%, -50%);
		background-color: #D61F24;
		border-radius: 50%;
		@media (max-width: 1920px){
			width: 24.74vw;
			height: 24.74vw;
			
		}
		@media (max-width: 1024px){
			width: 39.06vw;
			height: 39.06vw;
		}
		@media (max-width: 767px){
			width: 46.05vw;
			height: 46.05vw;
		}
	}
	&.js-active {
		opacity: 1;
		transform: scale(1);
	}
	&__swiper {
		width: 100%;
		min-height: 100%;
	}
	&__slide {
	}
	&__image {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 330px;
		height: 475px;
		transform: translate(-50%, -50%);
		@media (max-width: 1920px){
			width: 17.19vw;
			height: 24.74vw;
		}
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}

.right-screen4 {
	&__swiper-wrapper {
		margin-top: 40px;
		@media (max-width: 1920px){
			margin-top: 2.08vw;
		}
		@media (max-width: 1024px){
			display: flex;
			align-items: center;
			margin-top: 0;
		}
		@media (max-width: 767px){
			padding: 6.58vw 0;
		}
	}
	&__swiper {
		max-height: 510px;
		@media (max-width: 1920px){
			max-height: 26.56vw;
		}
		@media (max-width: 1024px){
			max-height: 100%;
		}
	}
	&__slide {
		padding: 20px 0;
		@media (max-width: 1920px){
			padding: 1.04vw 0;
		}
		@media (max-width: 1024px){
			padding: 4.56vw 0;
		}
		@media (max-width: 767px){
			padding: 0;
		}
	}
	.swiper-arrow {
	}
	.swiper-arrow-prev {
	}
	.swiper-arrow-next {
	}
}

.card-screen4 {
	cursor: pointer;
	display: flex;
	@media (max-width: 1024px){
		align-items: center;
		max-width: 54.69vw;
		margin: 0 auto;
	}
	@media (max-width: 767px){
		align-items: center;
		max-width: 95%;
		margin: 0 auto;
	}
	&__image {
		flex: 0 0 auto;
		width: 120px;
		height: 80px;
		transition: all 0.3s ease 0s;
		@media (max-width: 1920px){
			width: 6.25vw;
			height: 4.17vw;
		}
		@media (max-width: 1024px){
			width: 22.14vw;
			height: 14.97vw;
		}
		@media (max-width: 767px){
			width: 31.58vw;
			height: 21.05vw;
		}
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	&__body {
		flex: 1 1 auto;
		max-height: 80px;
		overflow: hidden;
		margin-left: 20px;
		transition: all 0.3s ease 0s;
		@media (max-width: 1920px){
			max-height: 4.17vw;
			margin-left: 1.04vw;
		}
		@media (max-width: 1024px){
			max-height: 100%;
			margin-left: 3.91vw;
		}
	}
	&__title {
		color: var(--6, #A9B0C6);
		font-size: 16px;
		font-style: normal;
		font-weight: 700;
		line-height: 105%; /* 20.8px */
		@media (max-width: 1920px){
			font-size: 0.83vw;
			
		}
		@media (max-width: 1024px){
			font-size: 1.95vw;
		}
	}
	&__text {
		margin-top: 20px;
		color: var(--2, #8F99BA);
		font-size: 12px;
		font-style: normal;
		font-weight: 500;
		line-height: 105%; /* 16.8px */
		@media (max-width: 1920px){
			margin-top: 1.04vw;
			font-size: 0.63vw;
		}
		@media (max-width: 1024px){
			font-size: 1.95vw;
			margin-top: 2.60vw;
		}
	}
	&__line {
		transition: all 0.3s ease 0s;
		position: absolute;
		width: 165px;
		height: 2px;
		background: linear-gradient(50deg, #D61F24 0%, rgba(231, 58, 40, 0.00) 100%);
		@media (max-width: 1920px){
			width: 8.59vw;
			height: 0.10vw;
		}
		@media (max-width: 1024px){
			width: 100%;
			flex: 1 1 auto;
			margin-left: 1.30vw;
		}
		&--side {
			position: relative;
		}
		&--bottom {
			left: 140px;
			bottom: 0;
			@media (max-width: 1920px){
				left: 7.29vw;

			}
		}

		&-dots {
			&::before{
				content:'';
				width: 1.43vw;
				height: 1.43vw;
				position: absolute;
				top: 50%; 
				left: 0;
				background-color: #D61F24;
				border-radius: 50%;
				transform: translate(0, -50%);
			}
		}
	}
	&.js-active {
		.card-screen4__image {
			width: 0;
		}
		.card-screen4__body {
			margin: 0;
			flex: 0 1 170px;
			position: relative;
			@media (max-width: 1920px){
				flex: 0 1 8.85vw;

			}
			&::before{
				content:'';
				position: absolute;
				top: 50%;
				left: -17px;
				background-color: #D61F24;
				width: 14px;
				height: 14px;
				@media (max-width: 1920px){
					left: -0.89vw;
					width: 0.73vw;
					height: 0.73vw;

				}
			}
		}
		.card-screen4__line {
			width: 100%;
		}
	}
}